}

.App {
    margin: auto;
}

:root {
    --color-primary: #0d253f;
    --color-secondary: #01b4e4;
    --color-tertiary: #90cea1;
    --color-grarient-secondaty-tertiary: linear-gradient(90deg, rgba(1, 180, 228, 1) 0%, rgba(144, 206, 161, 1) 72%);
    --color-grarient-tertiary-secondaty: linear-gradient(-90deg, rgba(1, 180, 228, 1) 0%, rgba(144, 206, 161, 1) 72%);
}

*::-webkit-scrollbar {
    width: 0.2em;
    height: 0.4em;
}

*::-webkit-scrollbar-track {
    background-color: #0d253f;
    background-color: var(--color-primary);
}

*::-webkit-scrollbar-thumb {
    background-color: #183f6c;
    border-radius: 10em;
}

* {
    margin: 0;
    font-family: "Quicksand", sans-serif;
    color: white; /* Safari/Chrome, other WebKit */ /* Firefox, other Gecko */
    box-sizing: border-box; /* Opera/IE 8+ */
}

*::selection {
    background: #0d253f;
    background: var(--color-primary);
    color: #fff;
}

* {
    caret-color: #0d253f;
    caret-color: var(--color-primary);
}

